<template>
     <div class="order">
                <div class="order__price">实付金额 <b>&yen;{{calculations.price}}</b></div>
                <div  v-show="showsubmit"
                class="order__btn" @click="()=>handlesubmit(true)"> 提交订单</div>
            </div>
            <div class="mask" v-show="showcon"
            @click="()=>handlesubmit(false)"
            >
                <div class="mask__content" @click.stop>
                    <h3 class="mask__content__title">确认要离开收银台？</h3>
                    <p class="mask__content__desc">请尽快完成支付，否则将被取消</p>
                    <div class="mask__content__btns">
                        <div class="mask__content__btn mask__content__btn--first"
                        @click="()=>handclickconorder(true)"> 取消订单</div>
                        <div class="mask__content__btn mask__content__btn--last"
                        @click="()=>handclickconorder(false)"> 确认订单</div>
                    </div>
                </div>
            </div>
</template>

<script>
import {ref} from 'vue'
import {useRoute,useRouter} from 'vue-router'
import {post} from '../../utils/request'
import {usecomcarteffect} from '../../effects/carteffects'
import {useStore} from 'vuex'


// 下单相关逻辑
const useordereffect =(shopid,shopname,productlist,addressid)=>{
    const store = useStore()
    const router = useRouter()
    //购物订单流程
    const  handclickconorder =async (iscanceled)  =>{
                const products = []
                for(let i in productlist.value){
                    const product = productlist.value[i]
                    products.push({id:parseInt(product._id,10) , num:product.count})
                }
               
            try{
                const result = await post('/api/order',{
                addressid,
                shopid,
                shopname:shopname.value,
                iscanceled,
                products
})
           
  if(result?.errno ===0){
    const cartList = JSON.parse(localStorage.cartList || '{}');
    delete cartList[shopid];
    localStorage.cartList = JSON.stringify(cartList);
   store.commit('clearcardata',shopid)
    router.push({name:'orderlist'})
  }else{
    // alert('失败')
  }
}catch(e){
    // showtoast('请求失败')
}
}
return{handclickconorder}
}

// 蒙层展示相关
const useshowmaskeffect =()=>{
    const showcon = ref(false)
    const handlesubmit =(status)=>{
           showcon.value = status
        }
        return {showcon,handlesubmit}
}

export default{
    name:'order',
    setup(){
       
        const route = useRoute()
       
        

        const shopid =parseInt(route.params.id,10)
        const {calculations,shopname,productlist} = usecomcarteffect(shopid)
        const {handclickconorder} = useordereffect(shopid,shopname,productlist,route.query.addressid)
       const    {showcon,handlesubmit} = useshowmaskeffect()
           
        return {showsubmit:!!route.query.addressid,
            showcon,calculations,handclickconorder,handlesubmit }
    }
}

</script>


<style lang="scss" scoped>
.order{
        position: absolute;
        left: 0;
        right: 0;
        bottom: 0;
        display: flex;
        height: 0.49rem;
        line-height: .49rem;
        background:#fff;
        &__price{
        flex: 1;
        text-indent: .24rem;
        font-size: .14rem;
        color: #333;
        }
        &__btn{
            width: 0.98rem;
            background: #4fb0f9;
            color: #fff;
            text-align: center;
            font-size: .14rem;
        }
    }
.mask{
    z-index: 1;
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    top: 0;
    background: rgba(0,0,0,0.50);
    &__content{
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%);
        border-radius: .04rem;
        width: 3rem;
        height: 1.56rem;
        text-align: center;
        background: #fff;
        &__title{
            margin: .24rem 0 0 0;
            line-height: .26rem;
            font-size: .18rem;
            color: #333;
           
        }
        &__desc{
            font-size: .14rem;
            color: #666666;
            margin: .08rem 0 0 0;
        }
        &__btns{
            display: flex;
            margin: .24rem .58rem 0 .58rem;
        }
        &__btn{
            flex: 1;
            width: 0.8rem;
            border: .01rem solid #4fb0f9;
            line-height: .32rem;
            border-radius: .16rem;
            font-size: .14rem;
           
            &--first{
                margin-right: .12rem;
                border: .01rem solid #4fb0f9;
                color: #4fb0f9;
            }
            &--last{
                margin-left: .12rem;
                background: #4fb0f9;
                color: #fff;
            }
        }
    }
}
</style>