<template>
    <div class="top">
            <div class="top__header">
              <div 
              class="iconfont top__header__back"
              @click="handlebackclick">&#xe6f2;</div>
              确认订单
            </div>
            <div class="top__receiver" @click="handaddressclick">
                <div class="top__receiver__title">收货地址</div>
                <div class="top__receiver__address">
                    {{hasaddress ? `${data.city}${data.department}${data.houseNumber}`:'请选择收货地址'}}</div>
                <div v-if="hasaddress"  class="top__receiver__info">
                    <span class="top__receiver__info__name">{{data.name}}</span>
                    <span class="top__receiver__info__number">{{data.phone}}</span>
                </div>
                <div class="iconfont top__receiver__icon">&#xe6f2;</div>
            </div>
        </div>
</template>


<script>
import {reactive} from 'vue';
import {get} from '../../utils/request'
import { onBeforeMount } from '@vue/runtime-core'
import { useRouter,useRoute} from 'vue-router'
export default{
    name:'toparea',
    setup(){
        const route = useRoute()
        const router = useRouter()
        const data = reactive({})
        const addressid = route.query.addressid;
        const handlebackclick = ()=>{
            router.back()
        }
        const handaddressclick = ()=>{
           router.push(`/addressselect?path=${route.path}`)
        }
        onBeforeMount(async()=>{
            if(addressid){
                const result = await get(`/api/user/address/${addressid}`)
                if(result?.errno ===0 ){
                    const resultdata = result.data;
                     data.city = resultdata.city
                     data.department = resultdata.department
                     data.houseNumber = resultdata.houseNumber
                     data.name = resultdata.name
                     data.phone = resultdata.phone
  }
            }
        })
        return {
            data,hasaddress:!!addressid,handlebackclick,handaddressclick }
    }
}
</script>


<style lang="scss" scoped>
.top{
    position: relative;
    height: 1.96rem;
    background-size:100% 1.59rem;
    background-image: linear-gradient(0deg,rgba(0,145,255,0.00)4%,#0091ff 50%);
    background-repeat:no-repeat ;
    &__header{
        position: relative;
        padding-top: .2rem;
        line-height: .24rem;
        color: #fff;
        text-align: center;
        font-size: .16rem ;
        &__back{
            position: absolute;
            font-size: .22rem;
        }
    }
    &__receiver{
        position: absolute;
        left: 0.18rem;
        right: 0.18rem;
        bottom: 0;
        height: 1.11rem;
        background: #fff;
        border-radius: .04rem;
        &__title{
            line-height: .22rem;
            padding: .16rem 0 .14rem .16rem;
            font-size: .16rem;
            color: #333;
        }
        &__address{
            line-height: .2rem;
            padding: 0 .4rem 0 .16rem;
            font-size: .14rem;
            color: #333;
        }
        &__info{
            padding: .06rem 0 0 .16rem;
            &__name{
                margin-right: .06rem;
                font-size: .12rem;
                color: #666;
                line-height: .18rem;
            }
        }
        &__icon{
            transform: rotate(180deg);
                position: absolute;
                right: 0.16rem;
                top: 0.5rem;
                color: #666;
                font-size: .2rem;
        }
    }
}
</style>