<template>
        <div class="products">
            <div class="products__title">{{ shopname }}</div>
            <div class="produucts__wrapper">
            <div class="products__list">
              
                <div class="products__item"
                v-for="item in productlist"
                 :key="item.id">
        <img class="products__item__img" :src="item.imgUrl" alt="">
        <div class="products__item__detail">
            <h4 class="products__item__title">{{item.name}}</h4>
            <p class="products__item__price">
                <span class="products__item__single">
                    <span class="products__item__yen">&yen;</span>
                        {{item.price}}x{{item.count}}
                </span>
                <span class="products__item__total">
                    <span class="products__item__yen">&yen;</span>
                        {{(item.price*item.count).toFixed(2)}}
                </span>
            </p>
        </div>
    </div>
    </div>
   </div>
</div>
</template>


<script>
import {useRoute} from 'vue-router'
import {usecomcarteffect} from '../../effects/carteffects'
export default{
    name:'productlists',
    setup(){
        const route = useRoute()
        const shopid = route.params.id
        const {productlist,shopname} = usecomcarteffect(shopid)
        return {productlist,shopname}
    }
}
</script>

<style lang="scss" scoped>
 @import '../../style/mixins.scss';
.products{
    margin: .16rem .18rem .55rem .18rem;
    background-color: #fff;
    &__title{
       padding: .16rem;
       font-size: .16rem;
       color: #333;
    }
    &__wrapper{
    margin:0 .18rem ;
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0.6rem;
    top: 2.6rem;
    overflow-y: scroll;
    }
   &__list{
    background: #fff;
   }
        &__item{
        position: relative;
        display: flex;
        padding:0 .16rem .16rem  .16rem;
        &__img{
            width: 0.46rem;
            height: 0.46rem;
            margin-right: .16rem;
        }
        &__detail{
           flex: 1;
        }
        &__title{
            margin: 0;
            line-height: 0.2rem;
            font-size: .14rem;
            color: #333;
            @include ellipsis;
        }
        &__price{
            display: flex;
            margin: .06rem 0 0 0 ;
            line-height: .2rem;
            font-size: .14rem;
            color: #e93b3b;
        }
        &__total{
           flex: 1;
           text-align: right;
           color: #333;
        }
        &__yen{
            font-size: .12rem;
        }
    }
    }

</style>
